import React, { useState, useEffect } from 'react';

const FetchSessionData = ({ onSessionData }) => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        console.log('Fetching session data...');
        const response = await fetch('http://127.0.0.1:8000/api/get-session-id/', {
          credentials: 'include', // Include authentication credentials (e.g., cookies)
        });
        if (!response.ok) {
          throw new Error(`Failed to fetch session data: ${response.status}`);
        }
        const data = await response.json();
        if (data.error) {
          throw new Error(data.error);
        }
        console.log('Fetched session data:', data);
        onSessionData(data); // Pass data (including sessionId) to parent component
      } catch (err) {
        console.error('Error fetching session data:', err.message || err);
        setError(err.message || 'An unknown error occurred.');      
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [onSessionData]);

  if (loading) {
    return <p>Loading session data...</p>;
  }

  if (error) {
    return <p style={{ color: 'red' }}>Error: {error}</p>;
  }

  return null; // No UI since data is passed to the parent
};

export default FetchSessionData;
